import { Component, Inject } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { AlertController, IonicPage, LoadingController, NavController, NavParams } from 'ionic-angular';

import { ErrorHandlerHelper } from '../../helpers';
import { SignupService } from './signup.service';

import { UserService } from '../../services';

import { limitsHelper, regexHelper } from '../../app/app.tokens';

@IonicPage({
  segment: 'entrance/signup'
})
@Component({
  selector: 'page-signup',
  templateUrl: 'signup.html',
  providers: [SignupService]
})
export class SignupPage {
  form: FormGroup;
  loading: any;
  nextPage: string;

  constructor(
    public alertCtrl: AlertController,
    private errorHandlerHelper: ErrorHandlerHelper,
    private fb: FormBuilder,
    public loadingCtrl: LoadingController,
    public navCtrl: NavController,
    public navParams: NavParams,
    private signupService: SignupService,
    private userService: UserService,
    @Inject(limitsHelper) private limits,
    @Inject(regexHelper) private regex
  ) {
    this.createForm();
  }

  createForm(){
    this.form = this.fb.group({
      email: ['', [
        Validators.required,
        Validators.pattern(this.regex.getEmailRegex())
      ]],
      password: ['', [
        Validators.required,
        Validators.minLength(this.limits.password.minLength)
      ]]
    });

  }

  signup(){
    const { email, password } = this.form.value;

    this.loading = this.loadingCtrl.create({
      content: 'Aguarde...'
    });

    this.loading.present();

    this.signupService
      .signup({ email, password })
      .then(user => {
        this.loading.dismiss();
        this.navCtrl.setRoot(this.nextPage || 'HomePage');
        return this.userService.createUser(user);
      })
      .catch(error => {
        console.error('Erro na page signup', error);

        this.loading.dismiss();

        const { title, message } = this.errorHandlerHelper
          .getFirebaseMessage(error);

        const alert = this.alertCtrl.create({
          title,
          message,
          buttons: ['Ok']
        });

        alert.present();
      });
  }
}



